@import "../../sass/variables.scss";
@import "../../sass/buttons";
.container {
  margin: 30px;
  padding: 40px 40px 0px 40px;
}

.gallery {
  width: 100%;
  height: 450px;
}

.pulseras {
  width: 100%;
}

.dInfo {
  background: $green-pastel-color;
  padding: 50px 50px 0px 50px;
  height: 875px;
}

.dNotification {
  background: $main-color;
  padding: 50px 40px 0px 40px;
  height: 875px;
}

.titlesDiv {
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  width: 382px;
  color: #ffff;
}

.txtDiv {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  color: #ffff;
}

.form {
  display: flex;
  flex-direction: column;
  width: 80%;
  height: 550px;
  overflow-y: scroll;
  padding: 0px 50px 0px 50px;
  margin-bottom: 10px;
}

.title {
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  color: $main-color;
}

.titleBlond {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
}

.txt {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
}

.txtBlond {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  margin-top: 15px;
}

.txtGreen {
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  color: $green-pastel-color;
}

.txtGreen span {
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  color: $green-pastel-color;
}

.sectionOne {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  border-top: 2px solid #c2c3c4;
  border-bottom: 2px solid #c2c3c4;
  margin: 20px 0px 10px 0px;
}

.sectionOne span {
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
}

.sectionTwo {
  display: flex;
  justify-content: space-between;
  margin-top: -15px;
}

.sectionTree {
  margin-top: 20px;
}

.sectionFour {
  margin: 30px 0px 30px 0px;
}

.sectionFour button {
  margin-top: 30px;
}

.colors {
  margin: 20px 0px 20px 0px;
}

.btnColors {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
}

div[disabled] {
  pointer-events: none;
  opacity: 0.7;
}

.customTextButton {
  @include textButtonC(18px);
}
.customOutlinedButton {
  @include outlinedAndContainedButtonC(16px, 600, 8px 16px, 8px);
  width: calc(50% - 10px);
  justify-content: flex-start !important;
}
.customContainedButton {
  @include outlinedAndContainedButtonC(16px, 600, 8px 16px, 8px);
  width: 100%;
}
