@import "../../sass/variables.scss";
@import "../../sass/typography";
@import "../../sass/layout";
@import "../../sass/buttons";
.hero {
  @include section;
  padding-top: 16rem;

  .ctnr {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 1520px;

    .textBox {
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: 696px;
      padding-bottom: 8rem;

      .headline {
        @include display;
        color: $main-color;

        &_semiboldUnderlined {
          color: $main-color;
          font-size: 6rem;
          font-weight: 600;
          position: relative;

          &::after {
            content: "";
            display: flex;
            position: absolute;
            width: 100%;
            height: 0.4rem;
            bottom: 10px;
            left: 0;
            z-index: 10;
            background-color: $green-pastel-color;
          }
        }
      }

      .subheading {
        @include subheading;
        padding-right: 10rem;

        &_underlined {
          text-decoration: 3px underline $green-pastel-color;
        }
      }
    }
  }
}

.hiw {
  @include section;
  padding-top: 118px;
  background-color: $smoke-color;

  .ctnr {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    max-width: 1520px;
    height: 100%;

    .headline {
      @include headline;
      color: $main-color;
    }

    .features {
      display: flex;
      justify-content: space-evenly;
      width: 100%;
      margin-top: 10rem;

      .feature:not(:last-child) {
        margin-right: 2.4rem;
      }
    }

    .feature {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      max-width: 400px;

      &Icon {
        display: flex;
        align-items: center;
        justify-content: center;
      }

      &TextBox {
        display: flex;
        flex-direction: column;

        .title {
          @include title;
          display: flex;
          justify-content: center;
          color: $main-color;
          text-align: center;
          height: 7.8rem;
        }

        .description {
          margin-top: 1.6rem;
          text-align: center;
        }
      }
    }
  }
}

.banner {
  @include section;
  background-color: $main-color;
  height: auto;
  min-height: auto;
  padding-top: 14rem;
  padding-bottom: 14rem;

  .ctnr {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    max-width: 1520px;
    height: 100%;
    justify-content: space-between;

    .textBox {
      display: flex;
      width: 100%;
      max-width: 932px;
      flex-direction: column;

      .title {
        font-size: 4.8rem;
        color: white;
      }

      .noPadding {
        margin-top: 3.2rem;
        padding: 0 !important;
      }
    }

    .image {
      margin-right: 5.5rem;
    }
  }
}

.benefits {
  @include section;
  padding-top: 118px;
  background-color: white;
  height: auto;
  min-height: auto;
  padding-top: 14rem;
  padding-bottom: 14rem;

  .ctnr {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    max-width: 1520px;
    height: 100%;

    .headline {
      @include headline;
      color: $main-color;
    }

    .benefit {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      &:first-of-type {
        margin-top: 8.5rem;
      }

      &:nth-child(odd) {
        flex-direction: row-reverse;

        .benefitTextBox {
          margin-right: initial;
          margin-left: 10rem;

          .title {
            color: $green-pastel-color;
          }
        }
      }

      &TextBox {
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 548px;
        margin-right: 10rem;

        .title {
          @include title;
          font-weight: 700;
          display: flex;
          justify-content: center;
          color: $main-color;
        }

        .description {
          @include subtitle;
          margin-top: 1.6rem;
        }
      }
    }
  }
}

.partners {
  @include section;
  padding-top: 118px;
  background-color: $smoke-color;
  height: auto;
  min-height: auto;
  padding-top: 8rem;
  padding-bottom: 14rem;

  .ctnr {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    max-width: 1520px;
    height: 100%;

    .headline {
      @include headline;
      color: $green-pastel-color;
    }

    .content {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      margin-top: 10rem;

      & img:not(:first-of-type) {
        margin-left: 12.4rem;
      }
    }
  }
}

.buy {
  @include section;
  padding-top: 118px;
  background-color: white;
  height: auto;
  min-height: auto;
  padding-top: 14rem;
  padding-bottom: 8rem;

  .ctnr {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    max-width: 1520px;
    height: 100%;

    .textBox {
      display: flex;
      align-items: center;
      width: 100%;
      max-width: 900px;
      flex-direction: column;

      .headline {
        text-align: center;
        font-size: 4.8rem;
        color: $main-color;
        line-height: 110%;
      }
      .subheading {
        @include subheading;
        font-weight: 500;
        text-align: center;
        max-width: 54rem;
      }
    }

    .content {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      margin-top: 10rem;

      & img:not(:first-of-type) {
        margin-left: 12.4rem;
      }
    }
  }
}

.comprarAhoraBtn {
  @include outlinedAndContainedButtonC(16px, 600, 8px 16px, 8px);
}
.crearPerfilBtn {
  @include textButtonC(32px);
  width: fit-content;
}
