@mixin textButtonC($fontSize) {
  padding: 0 !important;
  font-size: $fontSize !important;
}

@mixin outlinedAndContainedButtonC(
  $fontSize,
  $fontWeight,
  $padding,
  $borderRadius
) {
  font-size: $fontSize !important;
  padding: $padding !important;
  border-radius: $borderRadius !important;
  font-weight: $fontWeight !important;
}
