@import '../../../sass/variables.scss';

footer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: $main-color;
  padding-top: 6rem;
  padding-bottom: 6rem;
  padding-left: 5%;
  padding-right: 5%;
}

.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1520px;
  color: white;
}

.box {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.item {
  display: flex;
  flex-direction: column;
  text-align: right;
}

.item a {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  text-decoration-line: underline;
  color: #FF5151;
}

.title {
  font-style: normal;
  font-weight: 700;
  font-size: 2.6rem;
  color: white;
}

.txt {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  color: white;
}

.txt2 {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: white;
}

hr {
  margin-top: 1.8rem;
  margin-bottom: 1.8rem;
  border-top: 1px solid #fff;
}

.copyright {
  display: flex;
  justify-content: space-between;
}

.socialSection {
  display: flex;
  gap: 10px;
  align-items: center;
}

.prueba {
  display: flex;
  flex-direction: column;
}