@import "../../sass/variables.scss";
@import "../../sass/_buttons.scss";
.container {
  margin: 30px;
  padding: 40px 40px 0px 40px;
}

.box {
  display: flex;
  gap: 30px;
}

.item {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  width: 100%;
}

.svg {
  height: 550px;
  width: auto;
}

.title {
  font-family: $poppins-font;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  color: $main-color;
}

.miniBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
}

.miniItem {
  display: flex;
  flex-direction: column;
  text-align: justify;

  font-family: $poppins-font;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
}

.miniBox a {
  color: $green-pastel-color;
}

.txt {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  color: $main-color;
  margin-left: 5px;
}

.continuarBtn {
  @include outlinedAndContainedButtonC(16px, 600, 8px 16px, 8px);
}
