@import '../../../sass/variables.scss';

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  margin: 0 auto;
  padding: 10px;
  flex-direction: column;
}

.counter {
  display: flex;
  gap: 10px;
}

.counterInput {
  width: 50px;
  cursor: not-allowed;
}

.counterInput input{
  text-align: center;
  font-size: 18px;
}

.counterInput fieldset{
  border: none;
}

p{
  margin-bottom: 10px;
  font-size: 18px;
}