@import '../../../sass/variables.scss';

.listResult{
  display: flex;
  border: 1px solid $main-color;
  margin: 10px 0px 10px 0px;
  border-radius: 5px;
  justify-content: space-between;
  padding: 0px 10px 0px 10px;
}

.listResult div{
  margin-top: 10px;
}

.listResult button{
  margin-top: 5px;
}

.button{
  height: 45px;
}

.input{
  width: 100%;
}

.input input{
  font-family: $poppins-font;
  font-size: 16px;
}