@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

body,
p,
span,
a {
  font-weight: 400;
  color: $black-color;
  font-size: 2rem;
}

a {
  text-decoration: none;
  padding: none;
}

@mixin display {
  font-size: 6rem;
  font-weight: 400;
  line-height: 100%;
}

@mixin headline {
  font-size: 6rem;
  font-weight: 700;
}

@mixin subheading {
  font-size: 2.2rem;
}

@mixin label {
  font-size: 3rem;
  font-weight: 600;
}

@mixin title {
  font-size: 2.6rem;
  font-weight: 600;
}

@mixin subtitle {
  font-size: 2rem;
  font-weight: 600;
}